.site-header {
  .wrapper {
    padding: 0px 0;
    text-align: center;
    @include media-query($on-palm) {
      padding: 20px 0;
    }
  }
}

.site-title {
  float: none;
  font-family: $base-font-family;
  font-weight: 900;
  font-size: 35px;
  color: $grey-color-dark;
  letter-spacing: 1px;
  @include media-query($on-palm) {
    font-size: 30px;
  }
}

.site-subtitle{
  margin-top: 20px;
  font-family: $base-font-family;
  font-size: 18px;
  font-weight: 400;
  @include media-query($on-palm) {
    font-size: 16px;
  }
}




.single-post-nav {
  position: absolute;
  top: 20px;
  left: 15px;
  right: 15px;
  z-index: 2;
  color: $background-color;
  ul {
    width: 100%;
    margin: 0;
    list-style: none;
  }
  li {
    float: left;
    width: 33.33%;
    min-height: 1px;
    &:nth-child(2) {
      text-align: center;
    }
    &:nth-child(3) {
      text-align: right;
    }
    @include media-query($on-palm) {
      width: 50%;
      margin-left: 25%;
      margin-bottom: 10px;
      text-align: center;
      &:nth-child(1),
      &:nth-child(3) {
        display: none;
      }
    }
  }
  a {
    padding: 3px 10px;
    font-family: $base-font-family;
    color: $background-color;
    border: 1px solid $background-color;
    @include media-query($on-palm) {
      display: block;
      padding: 2px 10px;
      font-size: 14px;
    }
    &:hover {
      color: $text-color;
      background: $brand-color;
      border-color: $brand-color;
      text-decoration: none;
    }
  }
}





.single-post {
  margin-bottom: 30px;
}
.single-post-image {
  min-width: 100%;
}
.single-post-header {
  position: relative;
  padding: 5%;
  min-height: 150px;
  text-align: center;
  background: no-repeat center center fixed;
  background-color: $grey-color-dark;
  background-size: cover;
  @include media-query($on-palm) {
    min-height: 250px;
  }
  > div {
    position: absolute;
    bottom: 30px;
    width: 90%;
    @include media-query($on-palm) {
      bottom: 15px;
    }
  }
  &:before {
    content: "";
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
  }
}
.single-post-title, .single-post-meta {
  position: relative;
  z-index: 2;
  color: $background-color;
}
.single-post-title {
  margin-bottom: 60px;
  font-size: 50px;
  @include media-query($on-palm) {
    font-size: 35px;
    text-shadow: none;
  }
}
.single-post-meta {
  font-size: 20px;
  @include media-query($on-palm) {
    font-size: 16px;
  }
}
